@import "../../components/btnsLogin/btnsLogin";

.main__login {
  min-height: calc(100% - $font-size-small - ($space-small * 2));
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("../../asets/microscope-largre.jpg");
  background-position: right;
  background-size: cover;
  padding: 0;
  position: relative;
  z-index: -2;
  &:before {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: -1;
  }
  .login__title {
    color: white;
  }

  .loginForm {
    margin-top: $space-small;
    width: 90%;
    max-width: 400px;
    background-color: white;
    padding: $space-small;

    &__container {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__label {
      margin-bottom: $space-small/3;
      width: 50%;
      font-weight: $font-weight-bold;
    }
    &__input {
      margin: 0 auto;
      width: 50%;
      padding: 0.2rem;
      border: 0;
      border-bottom: 1px solid $color-black;
      background-color: rgba(0, 0, 0, 0);
    }
    &__error {
      width: 50%;
      height: $font-size-small + ($space-small/3) * 2;
      margin: 0 auto;
      padding: $space-small/3 0;
      font-size: $font-size-small;
      line-height: $font-size-small;
      color: $color-red;
    }
  }
}
